/* MUSIC ALBUM INDEX */

.musiflex {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 0;
  padding: 0;
  background-repeat: repeat;
}
.trackcontainer {
  flex: 1 1 360px;
  width: 250px;

  padding: 10px;
  margin: 0;
  // background: linear-gradient(230deg, rgba(218, 159, 142, 0.1), rgba(236, 0, 228, 0.1), rgba(0, 212, 236, 0.1), rgba(0, 236, 173, 0.1), rgba(110, 0, 236, 0.1));
  background-size: 1000% 1000%;
  
  -webkit-animation: AnimationName 30s ease infinite;
  -moz-animation: AnimationName 30s ease infinite;
  animation: AnimationName 30s ease infinite;

  h1, h2, h3, p {
    padding: 5px;
    background: linear-gradient(230deg, $color1, $color3);
    background-size: 1000% 1000%;
    -webkit-animation: AnimationName 30s ease infinite;
    -moz-animation: AnimationName 30s ease infinite;
    animation: AnimationName 30s ease infinite;
    padding: 10px;
    border-radius: 3px;

    box-shadow: 5px 10px 100px $color4;
    
  }
}
.track {
  width: 100%;
  background-size: cover;
  background-position: 50% 50%;
  min-height: 250px;
  padding: 0;
  margin: 0;
  border-radius: 3px;
  box-shadow: 5px 10px 100px $color4;

}

#privacy {
  position: fixed;
  bottom:0;
  left:0;
  z-index:77777;
  margin: 0;
  padding: 0;
  img {
    margin: 0;
    padding: 0;
    position: fixed;
    bottom: 0;
  }
}