/* Music Template */

#player {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  margin: 0;
  padding: 0;
  height: 100vh;

}

#controls {
  a {
    background-color: transparent;
    text-decoration: none;
    border: 0;
    
  }
  display: flex;
  flex: 0 0 200px;
  flex-wrap: wrap;
  position: fixed;
  top: 0px;
  left: 0px;
  padding: 15px;
  margin: 0 auto;



 .container {

  box-shadow: 5px 10px 100px $color4;
  
background: linear-gradient(230deg, $color1, $color3);
    
  background-size: 1000% 1000%;

  -webkit-animation: AnimationName 30s ease infinite;
  -moz-animation: AnimationName 30s ease infinite;
  animation: AnimationName 30s ease infinite;
  margin: 0 auto 0 0;
  
  text-align: center;
  border-radius: 20px;
  padding: 10px;
  }
}
button {
  background: linear-gradient(230deg, $color1, $color3);
  background-size: 1000% 1000%;
  color: $color5;
  -webkit-animation: AnimationName 30s ease infinite;
  -moz-animation: AnimationName 30s ease infinite;
  animation: AnimationName 30s ease infinite;
  width: 100px;
  height: 100px;
  border-radius: 100px;
  border-color: $color5;

  transition: background-color 0.5s ease-in-out;
}
button:hover {
  background-color: $color2;
}
#info {
  flex: 1 1 500px;
min-height: 80vh;
  padding: 15px;

  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}
#timer {
  flex: 1 1 200px;
  background-size: 1000% 1000%;
  
  -webkit-animation: AnimationName 30s ease infinite;
  -moz-animation: AnimationName 30s ease infinite;
  animation: AnimationName 30s ease infinite;
  padding: 15px;

  p {
    background-color: $color1;
    padding: 10px;
    border-radius: 3px;

    box-shadow: 5px 10px 100px $color4;
    
  }
  img {
    box-shadow: 5px 10px 100px $color4;
    
    border-radius: 3px;
  }
}
#tracktime {
  margin-bottom: auto;
}